.popup__title {
  width: 100%;
  font-family: 'Inter', Arial, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.2;
  margin: 0;
  text-align: center;
}

@media (max-width: 599px) {
  .popup__title {
    font-size: 18px;
    line-height: 1.22;
  }
}
